$dot_color: #8bbe56;
$border_color: #bacad3;
$r: 24px;
$dot_border: 3px;
$dot_spacing_x: 3px;
$dot_spacing_y: 4px;
$p_spacing: 3px;

.vis_text {
  max-width: 900px;
  display: block;
  margin: 2rem auto 1rem auto;
  text-align: center;
  line-height: 1.4;
  p {
    margin: 0 0 $p_spacing 0;
  }
}

.vis_intro {
  .inline_dot {
    display: inline-flex;
    align-items: baseline;
    justify-content: center;
    color: $dot_color;
    .sample_dot {
      height: $r;
      width: $r;
      display: inline-block;
      background-color: $dot_color;
      border-radius: 50%;
    }
  }
  .menu_signal {
    color: #223e7a;
  }
}

.export_button_main {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;

  .export_button {
    height: $r * 3;
    width: $r * 3;
    margin-top: 0;
    margin-right: 5px;
  }
  .export_button_explainer {
    width: 400px;
  }
}
.export_button_area {
  background-color: #7a7ca5;
  height: $r * 2;
  width: $r * 2;
  border-radius: 50%;
  border: $dot_border solid transparent;
  display: flex;
  align-items: center;
  img {
    width: 85%;
    margin: 0 auto;
  }
}
.export_button_area_small {
  background-color: #7a7ca5;
  height: $r;
  width: $r;
  border-radius: 50%;
  display: flex;
  align-items: center;
  border: $dot_border solid transparent;
  img {
    width: 85%;
    margin: 0 auto;
  }
}

.color_legend_box {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-bottom: 10px;

  .color_legend {
    max-width: 86vw;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    margin-bottom: 20px;
    padding: 10px 5px;
    font-size: 14px;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    #legend_category {
      margin-right: 25px;
    }
    #legend_colors {
      display: flex;
      flex-direction: row;
    }
    .color_guide_sample {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 10px;

      .color_box_sample {
        height: 20px;
        width: 20px;
        border-radius: 50%;
        margin-right: 3px;
      }
    }
  }
}
.dot_intervention_small {
  background-color: $dot_color;
  width: $r * 0.75;
  height: $r * 0.75;
  background-color: $dot_color;
  border: $dot_border solid transparent;
  border-radius: 50%;
  margin: $dot_spacing_y $dot_spacing_x;
}
.visualization_area {
  min-height: 50px;
  // margin-top: 20px;

  #array_export_area {
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .chart {
    width: 90%;
    margin: 0 auto;
  }
  .dot_intervention {
    background-color: $dot_color;
    width: $r;
    height: $r;
    background-color: $dot_color;
    border: $dot_border solid transparent;
    border-radius: 50%;
    margin: $dot_spacing_y $dot_spacing_x;
  }

  .border_top {
    border-top: 1px solid $border_color;
  }
  .border_right {
    border-right: 1px solid $border_color;
  }

  .study_list_button {
    padding-top: (2 * $dot_border)-1;
  }
  #chart_array {
    width: 18 * ($r + (2 * $dot_border) + (2 * $dot_spacing_x));
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .col_container {
    grid-template-rows: 1fr;
    .col_section {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }
  }
  .col_section_head,
  .matrix_section_head {
    font-size: 18px;
    align-self: center;
    // padding-left: 10px;
  }
  .row_outcome_type {
    font-size: 15px;
    margin: 0;
  }
  .row_arrow {
    font-size: 20px;
    margin-left: 30%;
  }
  .row_grid_box,
  .matrix_grid_box {
    padding: 5px;
    height: 100%;
  }
}
.row_grid_1x2 {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: 1fr;
  height: 100%;
}
.evidence_gap {
  font-size: 14px;
  font-weight: 600;
  color: #333;
}
