$scroll_bg_color: #efeaef;
$scroll_main_color: #8bbe56;

div#results_box,
div.filter_container {
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: $scroll_main_color $scroll_bg_color;
}

div#results_box::-webkit-scrollbar,
div.filter_container::-webkit-scrollbar {
  width: 12px;
}
div#results_box::-webkit-scrollbar-track,
div.filter_container::-webkit-scrollbar-track {
  background: $scroll_bg_color;
}
div#results_box::-webkit-scrollbar-thumb,
div.filter_container::-webkit-scrollbar-thumb {
  background-color: $scroll_main_color;
  border-radius: 20px;
  border: 3px solid $scroll_bg_color;
}
