.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 50px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 10px;
  border: 1px solid #888;
  width: 50%;
  max-height: 530px;
  overflow-x: hidden;
}

.modal_header {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
  .modal_header_img {
    width: 20px;
    margin-right: 5px;
    img {
      width: 100%;
    }
  }
  .modal_header_text {
    font-weight: 700;
    padding-bottom: 5px;
    color: #7a7ca5;
  }
}

.modal_filters,
.modal_studies {
  margin-bottom: 20px;
  width: 85%;
}
.modal_filters {
  margin: 0 auto;
  font-size: 16px;
  .modal_filter_group {
    margin-top: 5px;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
    padding-bottom: 7px;
    border-bottom: 2px solid #7a7ca5;
  }
  .modal_filter_category {
    flex: 1;
  }
  .modal_filter_value {
    flex: 1;
  }
}

.modal_studies {
  margin: 0 auto;
  padding-top: 20px;
  font-size: 14px;
  overflow-x: hidden;
  overflow-y: scroll;
  .modal_study {
    margin-top: 10px;
    display: grid;
    grid-template-columns: auto 1fr;
    column-gap: 10px;
  }
  .modal_study_info {
    border-bottom: 1px dashed #b1b3b6;
    padding-bottom: 4px;

    .modal_study_title {
      font-weight: 600;
    }
  }
}

.modal_single {
  padding-top: 20px;
  width: 90%;
  margin: auto;
  .study_tba {
    font-weight: 700;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #666;
  }
}
